.container_switch {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    gap: 0.2em;
    color:var(--text-primary);
    font-size: 1.2em;
    font-weight: bold;
    margin: 1em 0;
}

.switch {
    font-size: 17px;
    position: relative;
    display: inline-block;
    width: 3.5em;
    height: 2em;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--accent);
    transition: .4s;
    border-radius: 30px;
    outline: 1px solid var(--border);
}

.slider:before {
    position: absolute;
    content: "";
    height: 1.4em;
    width: 1.4em;
    border-radius: 20px;
    left: 0.3em;
    bottom: 0.3em;
    background-color: var(--text-secondary);
    transition: .4s;
}

input:checked+.slider {
    background-color: var(--accent);
}


input:checked+.slider:before {
    transform: translateX(1.5em);
}

.title{
    font-size: 0.6em;
}